<template>
  <div cote_shop>
    <!--头部面包屑以及返回首页  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home/hello' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片 -->
    <div class="shop_card">
      <el-card class="box-card">
        <!-- 搜索框 -->
        <div class="search_shp">
          <div class="search">
            <el-input
              placeholder="请输入内容"
              @clear="ajaxshop"
              clearable
              v-model="userParams.query"
              class="input-with-select"
            >
              <el-button slot="append" @click="ajaxshop" icon="el-icon-search"></el-button>
            </el-input>
          </div>
          <!-- 添加用户按钮 -->
          <div class="search_btn">
            <el-button type="primary">添加商品</el-button>
          </div>
        </div>
        <!-- 获取数据并且渲染 -->
        <div class="shop_sta">
          <template>
            <el-table :data="tableData" border style="width: 100%">
              <el-table-column type="index" label="#" width="40" class="table1"></el-table-column>
              <el-table-column prop="goods_name" label="商品名称" width="700"></el-table-column>
              <el-table-column prop="goods_price" label="商品价格(元)" width="110"></el-table-column>
              <el-table-column prop="goods_weight" label="商品重量" width="90"></el-table-column>
              <el-table-column prop="add_time" label="创建时间">
                <template slot-scope="scope">{{ scope.row.add_time | timefn }}</template>
              </el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button
                    type="primary"
                    size="mini"
                    icon="el-icon-edit"
                    circle
                    @click="shopedit(scope.row)"
                  ></el-button>
                  <el-button
                    type="danger"
                    size="mini"
                    icon="el-icon-delete"
                    circle
                    @click="shopdelete(scope.row.goods_id)"
                  ></el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <!-- 分页器 -->
        <div class="shop_pagina">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="userParams.pagenum"
            :page-sizes="[10, 15, 20, 25]"
            :page-size="userParams.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalnum"
          ></el-pagination>
        </div>
        <!-- 点击修改按钮弹出模态框编辑 -->
        <div class="shop_edit">
          <el-dialog title="编辑商品" :visible.sync="dialogVisible" width="50%">
            <el-form label-width="80px">
              <el-form-item label="商品名称">
                <el-input v-model="shopParams.goodsname"></el-input>
              </el-form-item>
              <el-form-item label="价格区域">
                <el-input v-model="shopParams.goodsprice"></el-input>
              </el-form-item>
              <el-form-item label="商品重量">
                <el-input v-model="shopParams.goodswith"></el-input>
              </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="addedit()">确 定</el-button>
            </span>
          </el-dialog>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      tableData: [],
      list: [],
      userParams: {
        query: "",
        pagenum: 1,
        pagesize: 10
      },
      totalnum: 0,
      dialogVisible: false,
      shopParams:{
        goodsname: "",
        goodsprice: "",
        goodswith: "",
        goodsid:"",
        goodsnum:''
      }
    };
  },
  created() {},
  mounted() {
    this.ajaxshop();
  },
  methods: {
    //获取商品列表
    async ajaxshop() {
      let { data: res } = await this.$axios.get("/goods", {
        params: this.userParams
      });
      console.log(res);
      this.tableData = res.data.goods;
      this.totalnum = res.data.total;
      console.log(this.tableData);
    },
    //分页
    handleSizeChange(size) {
      console.log(size);
      this.userParams.pagesize = size;
      this.ajaxshop();
    },
    handleCurrentChange(page) {
      console.log(page);
      this.userParams.pagenum = page;
      this.ajaxshop();
    },
    // 点击编辑弹出模态框
    shopedit(arr) {
      this.dialogVisible = true;
      this.shopParams.goodsid = arr.goods_id;
      this.shopParams.goodsname = arr.goods_name;
      this.shopParams.goodsprice = arr.goods_price;
      this.shopParams.goodswith = arr.goods_weight;
      this.shopParams.goodsnum = arr.goods_number;
      console.log(arr);
    },
    // 确定编辑修改商品数据
    async addedit() {
      let { data: res } = await this.$axios.put(`/goods/`+ this.shopParams.goodsid, {
        "goods_name": this.shopParams.goodsname,
        "goods_price": this.shopParams.goodsprice,
        "goods_weight": this.shopParams.goodswith,
        "goods_number":this.shopParams.goodsnum
      });
      console.log(res);
      this.$message({
        message: "修改商品成功",
        type: "success",
        duration: 1000
      });
      this.ajaxshop();
      this.dialogVisible = false;
    },
    // 点击删除按钮删除商品
    shopdelete(id) {
      console.log(id)
      this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          this.$axios.delete("/goods/" +id).then((res)=>{
            console.log(res)
            this.ajaxshop()
          })
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  }
};
</script>

<style scoped>
.el-dialog .el-form {
  bottom: 120px;
  position: static;
}
.search_shp {
  width: 100%;
  display: flex;
}
.shop_sta .el-table {
  font-size: 11px;
}
.shop_sta .table1 {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.search {
  width: 35%;
  display: flex;
  margin-right: 15px;
}
</style>
