<template>
  <div class="user_role">
    <!-- 头部面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home/hello' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片 -->
    <el-card class="box-card">
      <!-- 添加用户按钮 -->
      <div class="adduser">
        <el-button type="primary" @click="dialogVisible = true" >添加用户</el-button>
      </div>
      <!-- 添加用户的弹框 -->
      <div class="add_tan">
        <el-dialog title="添加角色" :visible.sync="dialogVisible" width="60%" height="44%">
          <el-form label-width="80px" :model="from">
            <el-form-item label="角色名称">
              <el-input v-model="from.Name"></el-input>
            </el-form-item>
            <el-form-item label="角色描述">
              <el-input v-model="from.Desc"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="ajaxadd()">确 定</el-button>
          </span>
        </el-dialog>
      </div>
      <!-- 用户列表 -->
      <div class="rolelist">
        <template>
          <el-table :data="tableData" border style="width: 100%">
            <!-- 展开 -->
            <el-table-column type="expand" width="40">
              <template slot-scope="scope">
                <el-row :class="['dbottom',i1=== 0 ?'dbtop':'','center']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                  <!-- 一级权限渲染 -->
                  <el-col :span="5">
                    <el-tag closable @close="open(scope.row,item1.id)">
                      {{ item1.authName }}
                    </el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!-- 二级和三级权限渲染 -->
                  <el-col :span="19">
                    <el-row :class="[i2 === 0 ?'':'dbtop','center']" v-for="(item2,i2) in item1.children" :key="item2.id">
                      <el-col :span="6">
                        <el-tag type="success" closable @close="open(scope.row,item2.id)">{{ item2.authName }}</el-tag>
                        <i class="el-icon-caret-right"></i>
                      </el-col>
                      <el-col :span="18">
                        <el-tag closable type="warning" @close="open(scope.row,item3.id)" v-for="(item3) in item2.children" :key="item3.id">
                          {{ item3.authName }}
                        </el-tag>
                      </el-col>
                    </el-row>
                  </el-col>
                </el-row>
              </template>
            </el-table-column>
            <!-- 索引列 -->
            <el-table-column type="index" label="#" width="40"></el-table-column>
            <el-table-column prop="roleName" label="角色名称"></el-table-column>
            <el-table-column prop="roleDesc" label="角色描述"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                  <el-button type="primary" size="mini" icon="el-icon-edit" circle @click="ajaxedit(scope.row)">编辑</el-button>
                  <el-button type="danger" size="mini" icon="el-icon-delete" circle @click="ajaxremove(scope.row.id)">删除</el-button>
                  <el-button type="warning" size="mini" icon="el-icon-star-off" circle @click="showSetRight(scope.row)">设置权限</el-button>    
              </template>
            </el-table-column>
          </el-table>
        </template>
      </div>
      <!-- 点击按钮修改用户 -->
      <div class="role_edit">
        <el-dialog
          title="修改用户"
          :visible.sync="show"
          width="60%"
          >
          <el-form label-width="80px">
            <el-form-item label="角色名称">
              <el-input v-model="roleName"></el-input>
            </el-form-item>
            <el-form-item label="活动区域">
              <el-input v-model="roleDesc"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="show = false">取 消</el-button>
            <el-button type="primary" @click="ajaxasync()">确 定</el-button>
          </span>
        </el-dialog>
      </div>
      <!-- 设置分配权限 -->
      <div class="showSetRight">
        <el-dialog
          title="分配权限"
          :visible.sync="showRight"
          width="50%"
          @close="setRightClose()"
          >
          <!-- 树形控件 -->
          <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all
          :default-checked-keys="deyKeys"
          ref="treeRef" 
          ></el-tree>
          <span slot="footer" class="dialog-footer">
            <el-button @click="showRight = false">取 消</el-button>
            <el-button type="primary" @click="allotRights()">确 定</el-button>
          </span>
        </el-dialog>
      </div>
    </el-card>
  </div>
        </template>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      from: {
        Name: "",
        Desc: ""
      },
      tableData: [],
      list:[],
      roleid:"",
      roleName:'',
      roleDesc:'',
      show:false,
      showRight:false,
      rightsList:[],
      treeProps:{
        label:'authName',
        children:'children'
      },
      // 默认选中节点的ID值数组
      deyKeys:[],
      roleId:''
    };
  },
  created() {},
  mounted() {
    this.ajaxrole();
  },
  methods: {
    //获取用户数据
    ajaxrole() {
      this.$axios.get("/roles").then(res => {
        console.log(res);
        this.tableData = res.data.data;
      });
    },
    //获取添加角色数据    
    ajaxadd() {
      this.$axios.post('/roles',{"roleName":this.from.Name,"roleDesc":this.from.Desc}).then((msg)=>{
          console.log(msg)
          this.list = this.tableData
          this.list = msg
          this.ajaxrole()
      })
      this.dialogVisible = false;
      this.from.Name = this.from.Desc =""
    },
    // 删除角色
    ajaxremove(id){
      this.$confirm('此操作将永久删除该数据, 是否继续?', '删除用户角色', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$axios.delete('/roles/' +id).then((res)=>{
            console.log(res)
            this.ajaxrole()
          })
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
    // 修改用户信息
    ajaxedit(a){
      this.show = true
      this.roleid = a.id
      this.roleName = a.roleName
      this.roleDesc = a.roleDesc
    },
    // 确定修改用户信息
    ajaxasync(){
        this.$axios.put(`/roles/${this.roleid}`,{"roleName":this.roleName,"roleDesc":this.roleDesc}).then((res)=>{
          console.log(res)
          this.$message({
            message:'修改角色成功',
            type:'success',
          })
          this.ajaxrole()
          this.show = false
        })
    },
    // 删除权限图标
    async open(role,rightId) {
        let confrom =  await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)
          if(confrom !=='confirm'){
            return this.$message.info('取消了删除')
          }else{
            this.$message({
              message:'删除成功',
              type:'success'
            })
          }    
        let {data:res} =await this.$axios.delete(`roles/${role.id}/rights/${rightId}`)
          console.log(res)
          role.children = res.data 
      },
      // 展示分配权限的弹框
      async showSetRight(role){
        this.roleId = role.id
        let { data:res} = await this.$axios.get('rights/tree')
        console.log(res)
        if(res.meta.status !== 200){
          return this.$message.info('获取数据权限失败')
        }
        this.rightsList = res.data
        console.log(this.rightsList)
        this.getLeveKeys(role,this.deyKeys)
        this.showRight = true
      },
      // 通过递归的方式获取所有三级权限的id
      getLeveKeys(node,arr){
        // 如果当前的节点不包含children属性，则是三级节点
        if(!node.children){
          return arr.push(node.id)
        }
        node.children.forEach(item=>this.getLeveKeys(item,arr))
      },
      // 监听分配权限对话框的关闭事件
      setRightClose(){
        this.deyKeys = []
      },
      // 点击事件给每个角色分配权限
      async allotRights(){
        let keys = [
          ...this.$refs.treeRef.getCheckedKeys(),
          ...this.$refs.treeRef.getHalfCheckedKeys()
        ]
        console.log(keys)
        let idStr = keys.join(`,`)
        console.log(idStr)
        let {data:res} = await this.$axios.post(`roles/${this.roleId}/rights`,{rids:idStr})
        console.log(res)
        this.$message({
          message:'设置权限成功',
          type:'success',
        })
        this.ajaxrole()
        this.showRight = false
      }
  }
};
</script>

<style scoped>
.el-tag{
  margin: 7px;
}
.el-row{
  width: 100%;
}
.dbtop{
  border-top: 1px solid #eee;
}
.dbottom{
  border-bottom: 1px solid #eee;
}
.cell .el-button.is-circle{
  width: 85px;
}
.box-card{
  margin-top: 15px;      
}
.el-dialog .el-form {
  bottom: 160px;
  position: static;
}
.el-table{
  margin-top: 20px;      
}
.center{
  display: flex;
  align-items: center;
}
.user_role .showSetRight .el-dialog{
  height: 45%;
}
</style>
