<template>
  <div class="user_name">
    <!-- 头部面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home/hello' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 中间的卡片 -->
    <el-card class="box-card">
      <!-- 搜索框和添加按钮 -->
      <div class="in-btn">
        <div class="text">
            <el-input @clear="ajaxlogin" placeholder="请输入内容" v-model="usersParams.query" clearable></el-input>
            <el-button icon="el-icon-search" @click="ajaxlogin()"></el-button>
        </div>
        <div class="add">
          <el-button type="primary" @click="dialogVisible = true">添加用户</el-button>
        </div>
      </div>
      <!-- 点击按钮添加用户数据 -->
      <div class="adduser">
        <el-dialog title="添加用户" :visible.sync="dialogVisible" width="60%">
          <el-form label-width="80px" :model="from">
            <el-form-item label="用户名">
              <el-input v-model="from.name"></el-input>
            </el-form-item>
            <el-form-item label="密码">
              <el-input v-model="from.pass"></el-input>
            </el-form-item>
            <el-form-item label="邮箱">
              <el-input v-model="from.elme"></el-input>
            </el-form-item>
            <el-form-item label="电话">
              <el-input v-model="from.phone"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="ajaxlist(from)">确 定</el-button>
          </span>
        </el-dialog>
      </div>
      <!-- 渲染接口数据列表 -->
      <template>
        <el-table border style="width: 100%" :data="tableData">
          <el-table-column type="index" label="#" width="50"></el-table-column>
          <el-table-column prop="username" label="姓名"></el-table-column>
          <el-table-column prop="email" label="邮箱"></el-table-column>
          <el-table-column prop="mobile" label="电话"></el-table-column>
          <el-table-column prop="role_name" label="角色"></el-table-column>
          <el-table-column label="状态">
            <template slot-scope="scope" @click="oppen">
              <el-switch
                @change="ajaxstate(scope.row.id,scope.row.mg_state)"
                v-model="scope.row.mg_state"
                active-color="#409EFF"
                inactive-color="#DCDFE6">
              </el-switch>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="primary" size="mini" icon="el-icon-edit" circle @click="ajaxedit(scope.row)"></el-button>
              <el-button type="danger" size="mini" icon="el-icon-delete" circle @click="open(scope.row.id)"></el-button>
              <el-button type="warning" size="mini" icon="el-icon-s-help" circle @click="ajaxquan(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <!-- 编辑 -->
      <div class="edit">
        <el-dialog
          title="编辑页面"
          :visible.sync="show"
          width="60%"
          >
          <el-form label-width="80px">
            <el-form-item label="用户名">
              <el-input v-model="userna" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="邮箱">
              <el-input v-model="email"></el-input>
            </el-form-item>
            <el-form-item label="手机号">
              <el-input v-model="mobile"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="show = false">取 消</el-button>
            <el-button type="primary" @click="addedit()">确 定</el-button>
          </span>
        </el-dialog>
      </div>
      <!-- 权限分配 -->
      <div class="ajaxquan">
        <el-dialog
            title="分配角色"
            :visible.sync="flag"
            width="50%"
            >
            <div>
              <p>当前的用户：{{ userInfo.username }}</p>
              <p>当前的角色：{{ userInfo.role_name }}</p>
              <p>分配新角色：
                <el-select v-model="selectRid" placeholder="请选择">
                  <el-option
                    v-for="item in rolesList"
                    :key="item.id"
                    :label="item.roleName"
                    :value="item.id">
                  </el-option>
                </el-select>
              </p>
            </div>
            <span slot="footer" class="dialog-footer">
              <el-button @click="flag = false">取 消</el-button>
              <el-button type="primary" @click="saveRoleInfo()">确 定</el-button>
            </span>
          </el-dialog>
      </div>
      <!-- 分页器 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="usersParams.pagenum"
        :page-sizes="[1, 2, 4, 8, 12]"
        :page-size="usersParams.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="Number(totalNum)"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      from:{
        name:'',
        pass:'',
        elme:'',
        phone:''
      },
      value:'',
      ajaxflag:false,
      totalNum:"",
      usersParams:{
        query:'',
        pagenum:1,
        pagesize:4
      },
      tableData:[],
      list:[],
      flag:false,
      show:false,
      b:'',
      userna:'',
      email:'',
      mobile:'',
      // 需要被分配角色的用户信息
      userInfo:{},
      rolesList:[],
      // 已选中的角色ID值
      selectRid:''
    };
  },
  created() {},
  mounted() {
    this.ajaxlogin()
  },
  methods: {
    // 获取用户列表
    ajaxlogin(){
      this.$axios.get(`/users?query=${this.usersParams.query}&pagenum=${this.usersParams.pagenum}&pagesize=${this.usersParams.pagesize}`).then((res)=>{
        console.log(res)
        this.tableData = res.data.data.users
        console.log(this.tableData)
        this.totalNum = res.data.data.total
        console.log(this.totalNum)
      })
    },
    // 添加用户
    ajaxlist(){
        this.$axios.post('/users',{
        "username":this.from.name,
        "password":this.from.pass,
        "email":this.from.elme,
        "mobile":this.from.phone,
      }).then((res)=>{
        console.log(res)
        this.list = this.tableData;
        this.list = res.data.meta
        if(this.from.name==""||this.from.pass==""||this.from.elme==""||this.from.phone==""){
          this.$message({
            type:'error',
            message:'数据不能为空'
          })
          this.dialogVisible = true
        }else{
          this.$message({
            type:'success',
            message:'添加用户成功'
          })
        }
        this.ajaxlogin()
        this.from.name=this.from.pass=this.from.emle=this.from.phone=""
      })
      this.dialogVisible = false
    },
     //分页器
    handleSizeChange: function(pagesize) {
      this.usersParams.pagesize = pagesize;
      console.log(this.size); //每页下拉显示数据
      this.ajaxlogin()
    },
    // 分页器
    handleCurrentChange: function(page) {
      this.usersParams.pagenum = page
      console.log(this.page); //点击第几页
      this.ajaxlogin()
    },
    // 展示分配角色
    async ajaxquan(userInfo){
      this.userInfo = userInfo
      let {data:res} = await this.$axios.get('/roles')
      console.log(res)
      this.rolesList = res.data
      this.flag = true
    },
    // 点击按钮分配角色
    async saveRoleInfo(){
      if(!this.selectRid){
        return this.$message.error('请选择要分配的角色')
      }
      let {data:res} = await this.$axios.put(`/users/${this.userInfo.id}/role`,{rid:this.selectRid})
      console.log(res)
      this.$message.success('修改角色成功')
      this.flag = false
      this.ajaxlogin()
    },
    // 删除
    open(id) {
        this.$confirm('此操作将永久删除该数据, 是否继续?', '您确定要删除吗', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$axios.delete('/users/' +id).then((res)=>{
            console.log(res)
            this.ajaxlogin()
          }) 
          this.$message({
           message:'删除成功',
           duration: 1500,
           type:'success'
          });
        }).catch(() => {
          this.$message({
           message:'已取消删除',
           duration: 1500,
           type:'info'
          });          
        });
      },
      // 编辑
      ajaxedit(a){
        this.show = true;
        this.b = a.id
        this.userna = a.username
        this.email = a.email
        this.mobile = a.mobile
        console.log(a)
      },
      // 确定编辑
      addedit(){
        this.$axios.put(`/users/${this.b}`,{"email":this.email,"mobile":this.mobile}).then((res)=>{
          console.log(res)
        })
        // console.log(f)
        this.$message({
          message:'更新成功',
          duration: 1500,
          type:'success'
        })
        this.show = false;
        this.ajaxlogin()
      },
      // 修改用户状态
      async ajaxstate(uId,bool){
        console.log(uId,bool)
        let {data:res} = await this.$axios.put(`users/${uId}/state/${bool}`)
        console.log(res)
        this.$message({
          type:'success',
          message:'修改用户状态成功',
          duration:1000
        })
        this.ajaxlogin()
      }
  }
};
</script>

<style lang="scss">
.ajaxquan p{
  margin-bottom: 15px;
}
.ajaxquan>.el-dialog{
  height: 40%;
}
.cell .el-button.is-circle{
  border-radius: 3px;
  width: 40px;
}
.el-pagination{
  margin-top: 15px;
}
.dialog-footer{
    // position: absolute;
    right: 20px;
    top: 80%;
}
.el-dialog .el-form{
    bottom: 80px;
    position: static;
}
.el-dialog__headerbtn {
  // position: absolute;
  top: 20px;
  right: 20px;
  padding: 0;
  background: 0 0;
  border: none;
  outline: 0;
  cursor: pointer;
  font-size: 16px;
}
.el-dialog__title {
  line-height: 24px;
  font-size: 18px;
  color: #303133;
}
.el-dialog__header {
  padding: 20px 20px 10px;
}
.user_name {
  position: relative;
}
.adduser {
  width: 100%;
  height: 100%;
}
.el-breadcrumb__inner {
  font-size: 12px;
}
.el-card {
  width: 100%;
  margin-top: 10px;
}
.in-btn {
  width: 100%;
  display: flex;
  justify-content: flex-start;
}
.text {
  display: flex;
}
.add {
  margin-left: 20px;
}
.text > .el-input {
  width: 290px;
}
.el-button {
  border-left: none;
}
.el-table {
  margin-top: 15px;
}
.el-table th > .cell {
  font-size: 12px;
}
</style>
