<template>
  <div>
    <!-- 头部面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home/hello' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>订单管理</el-breadcrumb-item>
      <el-breadcrumb-item>订单列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片 -->
    <div class="order_card">
      <el-card class="box-card">
        <!-- 搜索框 -->
        <div class="order_search">
          <el-input placeholder="请输入内容" clearable @clear="ajaxorder" v-model="orderParams.query" class="input-with-select">
            <el-button slot="append" icon="el-icon-search" @click="ajaxorder"></el-button>
          </el-input>
        </div>
        <!-- 表格 -->
        <template>
          <el-table :data="tableData" border style="width: 100%">
            <el-table-column type="index" label="#" width="60"></el-table-column>
            <el-table-column prop="order_number" label="订单编号" width="260"></el-table-column>
            <el-table-column prop="order_price" label="订单价格"></el-table-column>
            <el-table-column prop="is_send" label="是否付款">
                <template slot-scope="scope">
                    <el-button type="danger" size="mini" v-if="scope.row.is_send == '否'">未付款</el-button>
                    <el-button type="primary" size="mini" v-if="scope.row.is_send == '是'">已付款</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="is_send" label="是否发货"></el-table-column>
            <el-table-column prop="create_time" label="下单时间">
                <template slot-scope="scope">
                    {{ scope.row.create_time | timefn }}
                </template>
            </el-table-column>
            <el-table-column prop="date" label="操作">
                <template>
                    <el-button type="primary" size="mini" icon="el-icon-edit" circle @click="ajaxadderss"></el-button>
                    <el-button type="success" size="mini" icon="el-icon-location-outline" circle></el-button>
                </template>
            </el-table-column>
          </el-table>
        </template>
        <!-- 分页器 -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="orderParams.pagenum"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="orderParams.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
        <!-- 点击修改按钮修改地址 -->
        <div class="adderss">
          <el-dialog
          title="修改地址"
          :visible.sync="adderss"
          width="50%"
          >
          <el-form>
            <el-form-item before label="省市区/县">
              <el-select placeholder="请选择活动区域">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="详细地址">
              <el-input></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="adderss = false">取 消</el-button>
            <el-button type="primary" @click="adderss = false">确 定</el-button>
          </span>
        </el-dialog>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      orderParams: {
        query: "",
        pagenum: 1,
        pagesize: 5
      },
      tableData: [],
      total:0,
      adderss:false,
    };
  },
  created() {},
  mounted() {
    this.ajaxorder();
  },
  methods: {
    // 获取用户列表数据
    async ajaxorder() {
      let { data: res } = await this.$axios.get("/orders", {
        params: this.orderParams
      });
      console.log(res);
      this.tableData = res.data.goods;
      this.total = res.data.total
    },
    // 分页器
    handleSizeChange(size) {
      this.orderParams.pagesize = size
      this.ajaxorder();
    },
    handleCurrentChange(page) {
      this.orderParams.pagenum = page
      this.ajaxorder();
    },
    // 点击编辑按钮修改地址
    ajaxadderss(){
      this.adderss = true
    }
  }
};
</script>

<style scoped>
.order_search {
  display: flex;
  width: 35%;
}
.el-dialog .el-form{
    bottom: 140px;
}
.el-dialog .el-form .el-select{
  width: 100%;
}
</style>
