<template>
  <div class="shoplist">
    <!-- 头部面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home/hello' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片 -->
    <el-card class="box-card">
      <el-button type="primary">添加分类</el-button>
      <template>
        <el-table
          :data="tableData"
          border
          style="width: 100%"
          row-key="cat_id"
          :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
        >
          <el-table-column type="index" label="#" width="70" class="type"></el-table-column>
          <el-table-column prop="cat_name" label="分类名称" width="180"></el-table-column>
          <el-table-column prop="name" label="是否有效" width="180">
            <i class="el-icon-success"></i>
          </el-table-column>
          <el-table-column prop="cat_level" label="排序">
            <template slot-scope="scope">
              <el-button type="primary" size="mini" v-if="scope.row.cat_level == '0'">一级</el-button>
              <el-button type="success" size="mini" v-if="scope.row.cat_level == '1'">二级</el-button>
              <el-button type="warning" size="mini" v-if="scope.row.cat_level == '2'">三级</el-button>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="primary" size="mini" icon="el-icon-edit" circle class="edit" @click="listedit(scope.row)">编辑</el-button>
              <el-button type="danger" size="mini" icon="el-icon-delete" circle class="delete">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <!-- 分页器 -->
      <div class="list_page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="listParams.pagenum"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="listParams.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
      <!-- 点击编辑按钮弹出模态框 -->
      <div class="shop_dialong">
        <el-dialog title="提示" :visible.sync="dialogVisible" width="50%">
          <el-form label-width="80px">
            <el-form-item label="分类名称">
              <el-input v-model="shopParams.fenname"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="addedit">确 定</el-button>
          </span>
        </el-dialog>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      listParams: {
        query: "",
        pagenum: 1,
        pagesize: 5
      },
      tableData: [],
      list: [],
      total: 0,
      dialogVisible:false,
      shopParams:{
        fenname:"",
        fenid:""
      }
    };
  },
  created() {},
  mounted() {
    this.ajaxshoplist();
  },
  methods: {
    //商品分类数据
    async ajaxshoplist() {
      let { data: res } = await this.$axios.get("/categories", {
        params: this.listParams
      });
      // console.log(res);
      //       this.list = this.tableData
      this.tableData = res.data.result;
      this.total = res.data.total;
    },
    //分页器
    handleSizeChange(size) {
      this.listParams.pagesize = size;
      this.ajaxshoplist();
    },
    handleCurrentChange(page) {
      this.listParams.pagenum = page;
      this.ajaxshoplist();
    },
    //点击编辑
    listedit(arr){
      // console.log(arr)
      this.dialogVisible = true;
      this.shopParams.fenname = arr.cat_name;
      this.shopParams.fenid = arr.cat_id
      // console.log(this.shopParams)
    },
    //确定编辑
    async addedit(){
      console.log(this.shopParams.fenname)
         let {data:res} = await this.$axios.put('/categories/'+this.shopParams.fenid,{cat_name:this.shopParams.fenname})
         console.log(res)
         this.$message({
            type: 'success',
            message: '修改成功'
          });
         this.ajaxshoplist()
         this.dialogVisible=false
    }
  }
};
</script>

<style scoped>
.el-.el-table td div {
  font-size: 12px;
}
.el-icon-success {
  color: #39CE39;
}
.cell .el-button.is-circle {
  width: 80px;
}
.shop_dialong .el-dialog__wrapper .el-dialog{
  height: 30%;
}
.el-dialog .el-form[data-v-4380acee]{
  bottom: 200px;
}
</style>
