<template>
  <div class="login">
    <div data-v-7cf025fc class="login-container">
      <div data-v-7cf025fc class="login-box">
        <div data-v-7cf025fc class="avatar-box">
          <img data-v-7cf025fc src="/二狗子.png" alt />
        </div>
        <form data-v-7cf025fc class="el-form">
          <div data-v-7cf025fc class="el-form-item is-required">
            <div class="el-form-item__content" style="margin-left: 0px;">
              <div data-v-7cf025fc class="el-input el-input--prefix">
                <input
                  type="text"
                  autocomplete="off"
                  class="el-input__inner"
                  v-model="username"
                  value="admin"
                />
                <span class="el-input__prefix">
                  <i class="el-input__icon iconfont icon-user"></i>
                </span>
              </div>
            </div>
          </div>
          <div data-v-7cf025fc class="el-form-item is-required">
            <div class="el-form-item__content" style="margin-left: 0px;">
              <div data-v-7cf025fc class="el-input el-input--prefix">
                <input
                  type="password"
                  autocomplete="off"
                  class="el-input__inner"
                  v-model="password"
                  value="123456"
                />
                <span class="el-input__prefix">
                  <i class="el-input__icon iconfont icon-3702mima"></i>
                </span>
              </div>
            </div>
          </div>
          <div data-v-7cf025fc class="el-form-item btn-box">
            <div class="el-form-item__content" style="margin-left: 0px;">
              <button
                data-v-7cf025fc
                type="button"
                class="el-button el-button--primary"
                @click="ajaxlogin()"
              >
                <span>登录</span>
              </button>
              <button
                data-v-7cf025fc
                type="button"
                class="el-button el-button--info"
                @click="chong()"
              >
                <span>重置</span>
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      username: "admin",
      password: "123456"
    };
  },
  created() {},
  mounted() {
  },
  methods: {
    // 用户登录
    ajaxlogin() {
      this.$axios.post(`/login`, { "username": this.username, "password": this.password }).then(res => {
          console.log(res.data);
          if (this.username == "" || this.password == "") {
            this.$message({
              message: "用户名和密码不能为空",
              type: "warning"
            });
          } else {
            console.log(res.data.meta.msg);
            if (res.data.meta.msg == "登录成功") {
              this.$message({
                message: res.data.meta.msg,
                duration: 1500,
                type: "success"
              });
              sessionStorage.setItem("token", res.data.data.token);
              this.$router.push({
                path: "/home"
              });
            } else {
              this.$message({
                message: res.data.meta.msg,
                duration: 1500,
                type: "error"
              });
            }
          }
        });
    },
    chong(){
        this.username = this.password = ""
        this.$message({
           message: '重置成功',
           duration: 1500,
           type: "success"
        });
    }
  }
};
</script>


