<template>
  <div class="">
    <!-- 面包屑 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home/hello' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>分类参数</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 主题部分 -->
    <el-card class="box-card">
      <el-alert
        title="注意：只允许为第三级分类设置相关参数！"
        type="warning"
        effect="dark"
        show-icon
        :closable="false"
      >
      </el-alert>
      <!-- 选择商品分类 -->
      <el-row class="fl">
        <span>选择商品分类：</span>
        <el-cascader
        v-model="value"
         collapse-tags
          :options="list"
          :props="{ value: 'cat_id', label: 'cat_name',expandTrigger:'hover' }"
          @change="handleChange(value)"
        ></el-cascader>
      </el-row>
      <!-- 选项卡 -->
      <el-tabs type="border-card">
  <el-tab-pane label="动态参数"><!-- 按钮部分 -->
<el-button type="primary" size="mini" :disabled="flag" @click="addActive">添加参数</el-button>
<!-- 动态表格 -->
 <el-table
        :data="listActive"
        style="width: 100%;margin-bottom: 20px;"
        border
      >
        <el-table-column type="expand" width="50">
        </el-table-column>
        <el-table-column type="index" label="#" width=""> </el-table-column>
        <el-table-column prop="attr_name" label="参数名称" width="">
        </el-table-column>
        <!-- 操作按钮 -->
        <el-table-column label="操作" prop="attr_id" width="300">
          <template slot-scope="scope">
            <!-- 编辑 -->
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="update(scope.row)">编辑</el-button>
            <!-- 删除 -->
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="del(scope.row.attr_id)">删除</el-button>
            </template>
        </el-table-column>
      </el-table>
</el-tab-pane>
  <el-tab-pane label="静态属性" @tab-click="handleClick"><!-- 按钮部分 -->
<el-button type="primary" size="mini" :disabled="flag" @click="addActive2">添加属性</el-button>
<!-- 静态表格 -->
<el-table
        :data="listPublic"
        style="width: 100%;margin-bottom: 20px;"
        border
        stripe
      >
        <el-table-column type="expand" width="50">
        </el-table-column>
        <el-table-column type="index" label="#" width=""> </el-table-column>
        <el-table-column prop="attr_name" label="参数名称" width="">
        </el-table-column>
        <!-- 操作按钮 -->
        <el-table-column label="操作" prop="attr_id" width="300">
          <template slot-scope="scope">
            <!-- 编辑 -->
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="update2(scope.row)">编辑</el-button>
            <!-- 删除 -->
            <el-button size="mini" type="danger" icon="el-icon-delete"  @click="del(scope.row.attr_id)">删除</el-button>
            </template>
        </el-table-column>
      </el-table>
</el-tab-pane>
</el-tabs>
    </el-card>
     <!-- 动态添加的弹框 -->
    <el-dialog title="添加动态参数" :visible.sync="dialogFormVisible">
      <!-- 输入框部分 -->
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="动态参数" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
      </el-form>
      <!-- 按钮部分 -->
      <div slot="footer" class="dialog-footer">
        <!-- 这里的参数必须要是和ref一样的!!! -->
        <el-button @click="resetForm('ruleForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"
          >确 定</el-button
        >
      </div>
    </el-dialog>
     <!-- 静态添加的弹框 -->
    <el-dialog title="添加静态属性" :visible.sync="dialogFormVisible2">
      <!-- 输入框部分 -->
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="静态属性" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
      </el-form>
      <!-- 按钮部分 -->
      <div slot="footer" class="dialog-footer">
        <!-- 这里的参数必须要是和ref一样的!!! -->
        <el-button @click="resetForm('ruleForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm2('ruleForm')"
          >确 定</el-button
        >
      </div>
    </el-dialog>
   <!-- 动态修改的弹框 -->
    <el-dialog title="动态修改参数" :visible.sync="dialogFormVisibleUpdate">
      <!-- 输入框部分 -->
      <el-form
        :model="ruleFormUpdate"
        :rules="rules"
        ref="ruleFormUpdate"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="修改参数" prop="name">
          <el-input v-model="ruleFormUpdate.name"></el-input>
        </el-form-item>
      </el-form>
      <!-- 按钮部分 -->
      <div slot="footer" class="dialog-footer">
        <!-- 这里的参数必须要是和ref一样的!!! -->
        
        <el-button @click="resetForm('ruleFormUpdate')">取 消</el-button>
        <el-button type="primary" @click="updated"
          >确 定</el-button
        >
      </div>
    </el-dialog>
     <!-- 静态修改的弹框 -->
    <el-dialog title="静态修改属性" :visible.sync="dialogFormVisibleUpdate2">
      <!-- 输入框部分 -->
      <el-form
        :model="ruleFormUpdate"
        :rules="rules"
        ref="ruleFormUpdate"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="修改属性" prop="name">
          <el-input v-model="ruleFormUpdate.name"></el-input>
        </el-form-item>
      </el-form>
      <!-- 按钮部分 -->
      <div slot="footer" class="dialog-footer">
        <!-- 这里的参数必须要是和ref一样的!!! -->
        
        <el-button @click="resetForm('ruleFormUpdate')">取 消</el-button>
        <el-button type="primary" @click="updated2"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: {},
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      list: [],
      listActive:[],
      listPublic:[],
      //  选择商品分类所需
      ruleForm: {
        value: [],
        name: "",
      },
      total: 1000,
      type: "",
      // 选择商品分类所需
      value:[],
      active:'many',
      public:'only',
      flag:true,
      //  添加弹框所需
      dialogFormVisible: false,
      dialogFormVisible2: false,
      // 修改弹框
      dialogFormVisibleUpdate: false,
      dialogFormVisibleUpdate2: false,
      formLabelWidth: "100px",
       // 添加提示输入框
      ruleForm: {
        name: ""
      },
      // 修改提示输入框
      ruleFormUpdate: {
        name: "",
        id:0,
        cat_id:0
      },
      // 规则有这个才有小红点
      rules: {
        name: [
          { required: true, message: "参数名称不能为空", trigger: "blur" },
        ]
      },
      // 保存当前id
      id:0,
      // 选择当前页面
      xuan:'many',
      cat_id:0
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    handleClick(tab, event) {
        console.log(tab, event);
      },
      // 获取联级选择器数据
    async getList(num = 1, size = this.total) {
      let { data } = await this.$axios.get(
        `/categories?type=${this.type}&pagenum=${num}&pagesize=${size}`
      );
      this.total = data.data.total;
      this.list = data.data.result;
      console.log(this.list);
    },
    // 动态分类渲染获取数据
    async handleChange(i){
      console.log(i)
      console.log(this.id)
      if(this.id==0){
        let id = i[i.length-1]
       this.id = id
      }
      console.log(this.id)
      let {data:list} = await this.$axios.get(`/categories/${this.id}/attributes`,{
        params:{
          sel:this.active
        }
      })
      let {data:list2} = await this.$axios.get(`/categories/${this.id}/attributes`,{
        params:{
          sel:this.public
        }
      })
      // console.log(list.data,list2.data)
      this.listActive = list.data
      this.listPublic = list2.data
      // if(this.listActive.length!=0||this.listPublic.length!=0){
        this.flag=false
      // }
    },
  // 添加动态提交预校验
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.submitAdd()
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
     // 添加静态提交预校验
    submitForm2(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.submitAdd2()
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 动态确认提交
    async submitAdd(){
      console.log(111)
        let data = await this.$axios.post(`/categories/${this.id}/attributes`,{
          attr_name :this.ruleForm.name,
          attr_sel:this.active,
          attr_vals:''
        })
        this.$message({
          message:'添加成功',
          duration: 1000,
          type:'success'
        })
        console.log(data)
        this.dialogFormVisible = false;
        // 清空一下
        this.ruleForm.name=''
        this.handleChange(this.id)
    }
    ,
     // 静态确认提交
    async submitAdd2(){
        let data = await this.$axios.post(`/categories/${this.id}/attributes`,{
          attr_name :this.ruleForm.name,
          attr_sel:this.public,
          attr_vals:''
        })
        console.log(data)
        this.dialogFormVisible2 = false;
        this.ruleForm.name=''
        this.handleChange(this.id)
    }
    ,
     // 动态添加参数
    async addActive() {
      this.dialogFormVisible = true;
    },
    // 静态添加参数
    async addActive2() {
      this.dialogFormVisible2 = true;
    },
     // 重置触发
    resetForm(formName) {
      this.dialogFormVisible2 = false;
      this.dialogFormVisible = false;
      this.dialogFormVisibleUpdate2 = false;
      this.dialogFormVisibleUpdate = false;
      this.$refs[formName].resetFields();
    },
  // 删除事件
  del(id){
    // 删除
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          console.log(id);
          this.$axios.delete(`/categories/${this.id}/attributes/${id}`).then((res) => {
            console.log(res);
            // 刷新必须要放在里面
             this.handleChange()
          });
          this.$message({
            type: "success",
            message: "删除成功!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
  },
  // 修改的事件===========================================================================================================================================
  update(i){
    // console.log(i.id)
    console.log(i)
    this.ruleFormUpdate.id = i.attr_id
    this.ruleFormUpdate.name = i.attr_name
    this.ruleFormUpdate.cat_id = i.cat_id
    this.dialogFormVisibleUpdate=true
  },
  update2(i){
    this.ruleFormUpdate.id = i.attr_id
    this.ruleFormUpdate.name = i.attr_name
     this.ruleFormUpdate.cat_id = i.cat_id
    this.dialogFormVisibleUpdate2=true
  },
  // 确定修改
    async updated() {
      console.log(111)
      let { data } = await this.$axios.put(`/categories/${this.ruleFormUpdate.cat_id}/attributes/${this.ruleFormUpdate.id}`, {
        attr_name: this.ruleFormUpdate.name,
        attr_sel: this.active,
      });
      if (data.meta.status == 200) {
        this.$message({
          message: "更新成功！",
          type: "success",
        });
      }
      console.log(data.data);
      this.dialogFormVisibleUpdate = false;
      this.handleChange()
    },
    // 确定修改
    async updated2() {
      console.log(111)
      let { data } = await this.$axios.put(`/categories/${this.ruleFormUpdate.cat_id}/attributes/${this.ruleFormUpdate.id}`, {
        attr_name: this.ruleFormUpdate.name,
        attr_sel: this.public,
      });
      if (data.meta.status == 200) {
        this.$message({
          message: "更新成功！",
          type: "success",
        });
      }
      console.log(data.data);
      this.dialogFormVisibleUpdate2 = false;
      this.handleChange()
    },
  },
  mounted() {
    this.getList();
    //  console.log('list2的长度是',this.list.length)
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped lang="scss">
.el-row {
  display: flex;
  align-items: center;
  margin: 20px 0;
  .el-cascader {
    width: 250px;
  }
}
.fl{
  width: 100%;
}
.fl>span{
  width: 120px;
  font-size: 14px;
}
</style>
